@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
*,
::after,
::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}

button.button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
}

a.button,
button.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  background-color: #81b3d2;
  color: #ffffff;
  border-radius: 0;
  box-shadow: none;
  padding: 16px 40px;
  min-width: 180px;
  min-height: 58px;
  text-align: center;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  transition: 220ms all ease-in-out;
}
a.button:hover,
button.button:hover {
  background-color: #669ec0;
}
a.button:active,
button.button:active {
  background-color: #5496bd;
  color: #81b3d2;
}
a.button:disabled,
button.button:disabled {
  background-color: #e5e5e5;
}
a.button--block,
button.button--block {
  width: 100%;
  max-width: 458px;
  align-self: center;
}

.page__body {
  font-family: "PT Sans", "Helvetica", sans-serif;
  padding: 0;
  margin: 0;
  height: 100%;
  /*Сглаживание текста*/
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  background-color: #eeeeee;
}

.page__container {
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  min-height: 100vh;
  background-color: #ffffff;
}

.header {
  position: relative;
  min-height: 56px;
}

.header__logo {
  background-color: transparent;
  width: 185px;
  height: 95px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  cursor: pointer;
  z-index: 1;
}
@media (max-width: 767px) {
  .header__logo {
    top: calc(100% - 56px);
  }
}

@keyframes menu-open {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
.main-nav {
  position: relative;
}

.main-nav__toggle {
  cursor: pointer;
  font: inherit;
  display: block;
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  width: 63px;
  height: 56px;
  position: relative;
  float: right;
  z-index: 3;
  background-image: url("../../../img/icon-menu-open.svg");
  background-repeat: no-repeat;
  background-position: center;
  transition: 220ms all ease-in-out;
  color: #81b3d2;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle--close {
  background-image: url("../../../img/icon-menu-close.svg");
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__wrapper {
  z-index: 2;
  width: 100%;
  position: absolute;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    display: none;
  }
  .main-nav__wrapper--open {
    display: block;
    animation-name: menu-open;
    animation-duration: 0.3s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: static;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-flow: dense;
  }
  .main-nav__list::before {
    content: " ";
    display: block;
    grid-row: span 2;
    grid-column: 3/4;
  }
}

@media (max-width: 767px) {
  .main-nav--no-js .main-nav__toggle {
    display: none;
  }
  .main-nav--no-js .main-nav__wrapper {
    position: relative;
    display: block;
    margin-bottom: 56px;
  }
}
.site-list {
  list-style: none;
}

.site-list__item {
  display: block;
  position: relative;
  background-color: #81b3d2;
  color: #ffffff;
}
@media (max-width: 767px) {
  .site-list__item:not(:last-child)::after {
    content: "";
    display: block;
    height: 1px;
    box-shadow: inset 0 1px 0 rgba(1, 1, 1, 0.2);
  }
  .site-list__item:last-child::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
  .site-list__item:not(:first-child) {
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
}
@media (min-width: 768px) {
  .site-list__item {
    background-color: transparent;
    color: #000000;
  }
  .site-list__item--active {
    color: #766357;
  }
}

.site-list__item a {
  display: block;
  font: inherit;
  padding: 17px 56px 12px 56px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: inherit;
}
@media (min-width: 768px) {
  .site-list__item a {
    font-size: 14px;
    line-height: 21px;
    padding: 18px 20px 17px 20px;
  }
  .site-list__item a:hover {
    color: #81b3d2;
  }
  .site-list__item a:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

.main-container {
  flex-grow: 1;
  padding-bottom: 10px;
}
@media (min-width: 768px) {
  .main-container {
    padding-bottom: 0;
  }
}

