    margin: 0 auto;
  }
}
.introduction__wrapper--info::after {
  content: "";
  display: block;
  height: 37px;
  position: absolute;
  bottom: -21px;
  width: 100%;
  background-image: url("../../../img/intro-info-triangle.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
}
