.advantagers__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
}
@media (min-width: 768px) {
  .advantagers__list {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: dense;
  }
}

.advantagers__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 18px;
}
@media (max-width: 767px) {
  .advantagers__item:first-of-type {
    padding-top: 54px;
    padding-bottom: 40px;
  }
}
@media (min-width: 768px) {
  .advantagers__item {
    min-height: 256px;
    justify-content: flex-start;
    padding: 42px 25px;
  }
}
.advantagers__item--pre-photo {
  background-color: #81b3d2;
  color: #ffffff;
  padding-top: 39px;
  padding-bottom: 35px;
}
@media (min-width: 768px) {
  .advantagers__item--pre-photo:nth-of-type(2n) {
    padding: 43px 36px;
    padding-bottom: 41px;
    grid-column: 3/4;
  }
  .advantagers__item--pre-photo .advantages__description {
    max-width: 275px;
  }
  .advantagers__item--pre-photo:nth-of-type(2n) .advantages__description {
    max-width: 234px;
  }
}
.advantagers__item--photo {
  min-height: 260px;
  position: relative;
}
@media (min-width: 768px) {
  .advantagers__item--photo {
    grid-column: span 2;
    min-height: 256px;
  }
}
.advantagers__item--housing {
  display: grid;
  grid-template-columns: 40px auto;
  row-gap: 16px;
  justify-content: start;
  padding: 11px 18px;
  grid-auto-flow: dense;
  column-gap: 7px;
  padding-top: 23px;
}
@media (min-width: 768px) {
  .advantagers__item--housing {
    grid-template-columns: 1fr;
    justify-items: center;
    align-content: start;
    padding: 57px 18px 60px 18px;
    gap: 23px;
    min-height: 330px;
  }
}
.advantagers__item--housing::before {
  content: "";
  display: block;
  aspect-ratio: 1/1;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../../../img/hause.svg");
}
@media (max-width: 767px) {
  .advantagers__item--housing::before {
    grid-row: 1/3;
    grid-column: 1/2;
    align-self: start;
  }
}
@media (min-width: 768px) {
