.map {
  min-height: 350px;
  position: relative;
}
.map::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background-color: #81b3d2;
  border-color: #ffffff;
  content: "";
